@import url(https://cdnjs.cloudflare.com/ajax/libs/skeleton/2.0.4/skeleton.css);
.container {
  margin-top: 20px;
}
.navbar {
  background-color: #42b983;
  height: 50px;
}
#navbar-text {
  float: right;
  color: white;
  font-size: 3rem;
  margin-right: 20%;
  background-color: #42b983;
  font-weight: bold;
}
#home-intro {
  text-align: justify;
  color: #444;
  font-size: 2.3rem;
}
#questions .questions-header {
  text-align: center;
  font-size: 2rem;
}
#questions #question-display {
  display: block;
  background-color: lightblue;
  font-weight: bold;
  font-size: 1.8rem;
  border-radius: 3px;
}
#questions .question-input {
  margin-top: 8px;
  margin-bottom: 10px;
}
#show-friend {
  position: absolute;
  bottom: 20%;
  left: 10%;
  background-color: white;
  z-index: 2;
  border-radius: 5px;
  border-width: 0 1px 1px 1px;
  border-style: solid;
  border-color: #ccc;
  color: #555;
}
#show-friend .friend-photo {
    width: 300px;
}
#show-friend #show-friend-header {
    width: 100%;
    height: 35px;
    background-color: #42b983;
    border-radius: 3px;
    border-width: 1px 1px 0px 1px;
    border-style: solid;
    border-color: #ccc;
    margin: 0;
}
#show-friend #show-friend-header #close-button {
      font-size: 2rem;
      border: none;
      position: absolute;
      margin: 0;
      padding: 0;
      right: 5px;
}
#show-friend #show-friend-content {
    padding: 10px;
    text-align: center;
}
#show-friend #friend-profile h3 {
    font-weight: bold;
}














